$fontFamily: "Comfortaa", cursive;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Sora", sans-serif;
}

.selected {
  background: rgb(181, 217, 250);
}

.options {
  margin: 5px 0;
  padding: 8px;
  border: 1px solid rgb(167, 167, 167);
  border-radius: 8px;
  cursor: pointer;
  width: 100%;
  font-weight: 600;
  transition: 0.2s;
}

button {
  margin: 1rem 0;
  padding: 0.75rem;
}
